* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  font-size: 12px;
}

ul, li {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  padding: 23px 0;
}

.nav__list {
  list-style-type: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  padding: 23px 0;
  font-weight: 500;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
